const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {};
	},

	methods: {
		modifyPassword() {
			// 修改密码
			// 进入修改密码页
			uni.navigateTo({
				url: '/pages/forgetPassword'
			});
		},
		delCaching() {
			// 删除系统缓存
			uni.showModal({
				title: '提示',
				content: '确认删除缓存',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						uni.clearStorageSync();
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		outLogin() {
			// 退出系统--进入登录页
			uni.showModal({
				title: '提示',
				content: '是否退出系统',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						this.runOutLogin()
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		runOutLogin() {
			// 退出系统--进入登录页--执行
			uni.showLoading({
			  title: '退出系统...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/logout`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.state_code == 1) {
						uni.reLaunch({
							url: `/pages/login`
						})
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			})
		}
	}
};
